import "./App.css";
import React, { Component } from "react";
import BackToCCPS530 from "./components/BackToCCPS530";
import ProjectInfo from "./components/ProjectInfo";
import SignInForm from "./components/SignInForm";
import CourseInfo from "./components/CourseInfo";
import UniversityInfo from "./components/UniversityInfo";
import Footer from "./components/Footer";
import Dashboard from "./components/Dashboard";
import SignUpForm from "./components/SignUpForm";
import SignOut from "./components/SignOut";
import { BrowserRouter, Route, Redirect } from "react-router-dom";
import axios from 'axios';

import "bootstrap/dist/css/bootstrap.min.css";

class App extends Component {
  state = {
    UserAuthorzationBearerToken: null,
  };

  SignInAction = (Credentials) => {

    axios.post('/api/signin')
    .then((res) => {
      console.log(res);
    })
    .catch((err) => {
      console.log(err);
    });

    // let CurrentUserAuthorzationBearerToken = this.state
    //   .UserAuthorzationBearerToken;
    // let NewUserAuthorzationBearerToken = [
    //   ...CurrentUserAuthorzationBearerToken,
    //   Credentials,
    // ];
    // this.setState({
    //   UserAuthorzationBearerToken: NewUserAuthorzationBearerToken,
    // });
  };

  SignOutAction = (TheCallBack) => {
    this.setState(
      {
        UserAuthorzationBearerToken: null,
      },
      function () {
        TheCallBack();
      }
    );
  };

  render() {
    return (
      <BrowserRouter>
        <div className='App'>
          <BackToCCPS530 />
          <div className='container container-fluid'>
            <ProjectInfo name='Project - IP Geolocation' />
            {/* {JSON.stringify(this.state.UserAuthorzationBearerToken)} */}

            <Route exact path='/'>
              {this.state.UserAuthorzationBearerToken === null ? (
                <Redirect to='/signin' />
              ) : (
                <Dashboard />
              )}
            </Route>

            <Route exact path='/signin'>
              <SignInForm SignInAction={this.SignInAction} />
            </Route>

            <Route exact path='/signout'>
              <SignOut SignOutAction={this.SignOutAction} />
            </Route>

            <Route exact path='/signup'>
              <SignUpForm />
            </Route>

            <Route>
              {this.state.UserAuthorzationBearerToken === null ? (
                <Redirect to='/signin' />
              ) : (
                <Dashboard />
              )}
            </Route>

            <CourseInfo />
            <UniversityInfo />
            <Footer />
          </div>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
